export default {
	name: {
		rules: [
			//  name 
			{
				required: true,
				errorMessage: '',
			}
		],
		validateTrigger: 'submit'
	},
	phone: {
		rules: [
			//  name 
			{
				required: true,
				errorMessage: '',
			},
			{
				validateFunction:function(rule,value,data,callback){
					if(RegExp(/^1[34578]\d{9}$/).test(value)) return true
					else callback('')
			  }
			}
		],
		validateTrigger: 'submit'
	},
	address: {
		rules: [
			//  name 
			{
				required: true,
				errorMessage: '',
			}
		],
		validateTrigger: 'submit'
	},
	house: {
		rules: [
			//  name 
			{
				required: true,
				errorMessage: '',
			}
		],
		validateTrigger: 'submit'
	}
}